export default {
  data: () => ({
    content: [],
    selection: 0,
    quote: '',
    author: '',
    loader: false
  }),
  async fetch() {
    this.loader = true
    await this.$axios.$get('/sanctum/csrf-cookie')
    const res = await this.$axios.$get(`api/news/item`)
    this.blogs_load = false
    this.content = res.data
    this.loader = false
  },

  async mounted() {},
  computed: {},
  methods: {}
}
