export default {
  data: () => ({
    quote: '',
    author: '',
    loader: false
  }),
  async fetch() {
    this.loader = true
    await this.$axios.$get('/sanctum/csrf-cookie')
    const res = await this.$axios.$get(`api/quotes/item`)
    this.quote = res.data['message']
    this.author = res.data['author']
    this.loader = false
  },

  async mounted() {},
  computed: {},
  methods: {}
}
